"use client";

import type * as PhaserType from "phaser";
import { useEffect, useRef, useState } from "react";
import { buildingPalette, groundTilePalette } from "@/lib/mapConfig";
import type { CityMapData } from "@/lib/mapStore";

const MAP_TILE_SIZE = 64;
const PINCH_SCALE = 0.95;
const PLAYER_SPRITE_KEY = "warriorblue-walk";
const PLAYER_ANIM_KEY = "warriorblue-walk-loop";
const PLAYER_FRAME_WIDTH = 192;
const PLAYER_FRAME_HEIGHT = 192;
const PLAYER_TARGET_HEIGHT = 150;
const PLAYER_SCALE = PLAYER_TARGET_HEIGHT / PLAYER_FRAME_HEIGHT;
const PLAYER_SPEED = 220;

const TILE_SOURCES = groundTilePalette.map((tile, index) => ({
  id: tile.id,
  key: `city-tile-source-${index}`,
  path: tile.image
}));

const BUILDING_SOURCES = buildingPalette.map((building) => ({
  id: building.id,
  key: building.key,
  path: building.image
}));

type MapResponse = {
  map: CityMapData;
};

const dPad = [
  { label: "↑", dx: 0, dy: -1, className: "pad-up" },
  { label: "↓", dx: 0, dy: 1, className: "pad-down" },
  { label: "←", dx: -1, dy: 0, className: "pad-left" },
  { label: "→", dx: 1, dy: 0, className: "pad-right" }
] as const;

type NPCRecord = {
  sprite: PhaserType.GameObjects.Sprite;
  label: PhaserType.GameObjects.Text;
  target: PhaserType.Math.Vector2;
  name: string;
};

export function CityPhaser() {
  const ref = useRef<HTMLDivElement | null>(null);
  const [mapData, setMapData] = useState<CityMapData | null>(null);
  const [feedback, setFeedback] = useState<string | null>(null);
  const [showPad, setShowPad] = useState(false);
  const [orientationHint, setOrientationHint] = useState(false);
  const [virtualDirection, setVirtualDirection] = useState({ dx: 0, dy: 0 });
  const virtualDirectionRef = useRef({ dx: 0, dy: 0 });

  useEffect(() => {
    virtualDirectionRef.current = virtualDirection;
  }, [virtualDirection]);

  useEffect(() => {
    const handleResize = () => {
      if (typeof window === "undefined") return;
      const width = window.innerWidth;
      const height = window.innerHeight;
      setShowPad(width < 900);
      setOrientationHint(width < 700 && height > width);
    };
    handleResize();
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  useEffect(() => {
    let active = true;
    async function loadMap() {
      try {
        const response = await fetch("/api/city/map", { cache: "no-store" });
        if (!response.ok) {
          throw new Error(await response.text());
        }
        const data = (await response.json()) as MapResponse;
        if (active) {
          setMapData(data.map);
        }
      } catch (err) {
        if (active) {
          setFeedback(err instanceof Error ? err.message : "Falha ao carregar o mapa.");
        }
      }
    }
    void loadMap();
    return () => {
      active = false;
    };
  }, []);

  useEffect(() => {
    if (!ref.current || !mapData) return;
    let game: PhaserType.Game | null = null;

    (async () => {
      const Phaser = await import("phaser");
      const mapReference = mapData;

      class CityScene extends Phaser.Scene {
        private cursors!: Phaser.Types.Input.Keyboard.CursorKeys;
        private player!: Phaser.GameObjects.Sprite;
        private floorLayer?: Phaser.Tilemaps.TilemapLayer;
        private detailLayer?: Phaser.Tilemaps.TilemapLayer;
        private map?: Phaser.Tilemaps.Tilemap;
        private npcs: NPCRecord[] = [];
        private worldWidth = 0;
        private worldHeight = 0;

        preload() {
          [...TILE_SOURCES, ...BUILDING_SOURCES].forEach((source) => {
            this.load.image(source.key, source.path);
          });
          this.load.spritesheet(PLAYER_SPRITE_KEY, "/sprites/warriorblue/walk.png", {
            frameWidth: PLAYER_FRAME_WIDTH,
            frameHeight: PLAYER_FRAME_HEIGHT
          });
        }

        create() {
          this.buildFloor();
          this.createPlayer();
          this.spawnNPCs();
          this.cursors = this.input.keyboard!.createCursorKeys();
          this.cameras.main.setBounds(0, 0, this.map!.widthInPixels, this.map!.heightInPixels);
          this.cameras.main.startFollow(this.player, true, 0.2, 0.2);
        }

        private buildFloor() {
          this.createFloorTexture();
          this.map = this.make.tilemap({
            data: mapReference.ground,
            tileWidth: MAP_TILE_SIZE,
            tileHeight: MAP_TILE_SIZE
          });
          const tileset = this.map.addTilesetImage(
            "city-floor",
            "city-floor",
            MAP_TILE_SIZE,
            MAP_TILE_SIZE,
            0,
            0
          );
          if (!tileset) {
            throw new Error("Tileset da cidade não pôde ser carregado.");
          }
          this.floorLayer = this.map.createLayer(0, tileset) ?? undefined;
          this.floorLayer?.setOrigin(0);

          if (mapReference.detail) {
            const detailMap = this.make.tilemap({
              data: mapReference.detail,
              tileWidth: MAP_TILE_SIZE,
              tileHeight: MAP_TILE_SIZE
            });
            this.detailLayer = detailMap.createLayer(0, tileset) ?? undefined;
            this.detailLayer?.setOrigin(0);
            this.detailLayer?.setDepth(5);
          }
          this.worldWidth = this.map.widthInPixels;
          this.worldHeight = this.map.heightInPixels;
          this.renderBuildings();
        }

        private createFloorTexture() {
          if (this.textures.exists("city-floor")) return;
          const canvas = this.textures.createCanvas(
            "city-floor",
            MAP_TILE_SIZE * TILE_SOURCES.length,
            MAP_TILE_SIZE
          );
          if (!canvas) return;
          const ctx = canvas.getContext();
          if (!ctx) return;
          TILE_SOURCES.forEach((source, index) => {
            const sourceImage = this.textures.get(source.key).getSourceImage() as HTMLImageElement;
            ctx.drawImage(
              sourceImage,
              0,
              0,
              sourceImage.width,
              sourceImage.height,
              index * MAP_TILE_SIZE,
              0,
              MAP_TILE_SIZE,
              MAP_TILE_SIZE
            );
          });
          canvas.refresh();
        }

        private renderBuildings() {
          mapReference.buildings.forEach((row, rowIndex) => {
            row.forEach((cell, columnIndex) => {
              if (cell <= 0) return;
              const def = BUILDING_SOURCES.find((building) => building.id === cell);
              if (!def) return;
              const sprite = this.add.image(
                columnIndex * MAP_TILE_SIZE + MAP_TILE_SIZE / 2,
                rowIndex * MAP_TILE_SIZE + MAP_TILE_SIZE,
                def.key
              );
              sprite.setOrigin(0.5, 1);
              sprite.setDepth(25 + rowIndex);
            });
          });
        }

        private createPlayer() {
          if (!this.anims.exists(PLAYER_ANIM_KEY)) {
            this.anims.create({
              key: PLAYER_ANIM_KEY,
              frames: this.anims.generateFrameNumbers(PLAYER_SPRITE_KEY, { start: 0, end: 5 }),
              frameRate: 8,
              repeat: -1
            });
          }
          this.player = this.add.sprite(0, 0, PLAYER_SPRITE_KEY, 0);
          this.player.setOrigin(0.5, 1);
          this.player.setScale(PLAYER_SCALE);
          this.player.setDepth(30);
          this.player.setTint(0x8dd9ff);
          this.player.setPosition(this.worldWidth / 2, this.worldHeight / 2);
        }

        private spawnNPCs() {
          const names = ["Sentinela", "Guarda", "Explorador", "Mercador"];
          const hueOffsets = [0x5dade2, 0xffa07a, 0xfff176, 0xa569bd];
          for (let i = 0; i < 4; i++) {
            const sprite = this.add.sprite(
              Phaser.Math.Between(120, this.worldWidth - 120),
              Phaser.Math.Between(120, this.worldHeight - 120),
              PLAYER_SPRITE_KEY,
              Phaser.Math.Between(0, 5)
            );
            sprite.setOrigin(0.5, 1);
            sprite.setScale(PLAYER_SCALE);
            sprite.setTint(hueOffsets[i % hueOffsets.length]);
            sprite.play(PLAYER_ANIM_KEY);
            sprite.setDepth(22);
            const level = Phaser.Math.Between(3, 12);
            const name = `${names[i % names.length]} Lv.${level}`;
            const label = this.add
              .text(sprite.x, sprite.y - sprite.displayHeight - 4, name, {
                color: "#fff8d0",
                fontSize: "14px",
                fontStyle: "bold"
              })
              .setOrigin(0.5, 1);
            const npc: NPCRecord = {
              sprite,
              label,
              target: this.randomPoint(),
              name
            };
            this.scheduleSpeech(npc);
            this.npcs.push(npc);
          }
        }

        private scheduleSpeech(npc: NPCRecord) {
          const messages = [
            "Patrulha em andamento.",
            "A cidade precisa de você.",
            "Alguma troca?",
            "Boas vindas!"
          ];
          this.time.addEvent({
            delay: Phaser.Math.Between(4000, 9000),
            callback: () => {
              const message = messages[Phaser.Math.Between(0, messages.length - 1)];
              const text = this.add
                .text(npc.sprite.x, npc.sprite.y - npc.sprite.displayHeight - 8, message, {
                  color: "#fff3d4",
                  fontSize: "13px",
                  backgroundColor: "rgba(0,0,0,0.6)",
                  padding: { x: 8, y: 4 }
                })
                .setOrigin(0.5, 1)
                .setAlpha(0);
              this.tweens.add({
                targets: text,
                alpha: 1,
                duration: 150,
                yoyo: true,
                hold: 2000,
                completeDelay: 300,
                onComplete: () => text.destroy()
              });
              this.scheduleSpeech(npc);
            }
          });
        }

        private randomPoint() {
          return new Phaser.Math.Vector2(
            Phaser.Math.Between(80, this.worldWidth - 80),
            Phaser.Math.Between(80, this.worldHeight - 80)
          );
        }

        private updateNPCs(delta: number) {
          const speed = 60;
          this.npcs.forEach((npc) => {
            const dir = new Phaser.Math.Vector2(npc.target.x - npc.sprite.x, npc.target.y - npc.sprite.y);
            if (dir.length() < 10) {
              npc.target = this.randomPoint();
              return;
            }
            dir.normalize();
            npc.sprite.x += dir.x * speed * (delta / 1000);
            npc.sprite.y += dir.y * speed * (delta / 1000);
            npc.sprite.setFlipX(dir.x < 0);
            npc.label.setPosition(npc.sprite.x, npc.sprite.y - npc.sprite.displayHeight - 4);
          });
        }

        update(_: number, delta: number) {
          if (!this.player || !this.cursors || !this.map) return;

          let dx = 0;
          let dy = 0;

          if (this.cursors.left?.isDown) dx -= 1;
          if (this.cursors.right?.isDown) dx += 1;
          if (this.cursors.up?.isDown) dy -= 1;
          if (this.cursors.down?.isDown) dy += 1;

          const virtual = virtualDirectionRef.current;
          dx += virtual.dx;
          dy += virtual.dy;

          const length = Math.hypot(dx, dy) || 1;
          dx /= length;
          dy /= length;

          const nextX = Phaser.Math.Clamp(
            this.player.x + dx * PLAYER_SPEED * (delta / 1000),
            this.player.displayWidth / 2,
            this.worldWidth - this.player.displayWidth / 2
          );
          const nextY = Phaser.Math.Clamp(
            this.player.y + dy * PLAYER_SPEED * (delta / 1000),
            this.player.displayHeight / 2,
            this.worldHeight
          );

          const collided = this.npcs.some(
            (npc) => Phaser.Math.Distance.Between(npc.sprite.x, npc.sprite.y, nextX, nextY) < 40
          );

          if (!collided) {
            this.player.setPosition(nextX, nextY);
          }

          if (dx !== 0 || dy !== 0) {
            this.player.setFlipX(dx < 0);
            this.player.anims.play(PLAYER_ANIM_KEY, true);
          } else if (this.player.anims.currentAnim) {
            this.player.anims.stop();
            this.player.setFrame(0);
          }

          this.updateNPCs(delta);
        }
      }

      const getSize = () => {
        const containerWidth = ref.current?.clientWidth ?? window.innerWidth ?? 960;
        const maxWidth = Math.min(containerWidth, 1200);
        const width = Math.max(640, maxWidth * PINCH_SCALE);
        const height = Math.round(width * (9 / 16));
        return { width, height };
      };

      const initialSize = getSize();
      game = new Phaser.Game({
        type: Phaser.AUTO,
        width: initialSize.width,
        height: initialSize.height,
        parent: ref.current!,
        backgroundColor: "#1a0a05",
        scene: CityScene,
        render: { pixelArt: true, antialias: false }
      });

      const handleResize = () => {
        const size = getSize();
        game?.scale.resize(size.width, size.height);
      };

      window.addEventListener("resize", handleResize);
      const cleanup = () => window.removeEventListener("resize", handleResize);
      game.events?.once("destroy", cleanup);
    })();

    return () => {
      if (game) {
        game.destroy(true);
      }
    };
  }, [mapData]);

  const startDirection = (dx: number, dy: number) => () => setVirtualDirection({ dx, dy });
  const stopDirection = () => setVirtualDirection({ dx: 0, dy: 0 });

  if (feedback) {
    return <p>{feedback}</p>;
  }

  if (!mapData) {
    return <p>Carregando mapa…</p>;
  }

  return (
    <div className="relative">
      <div className="phaser-wrapper" ref={ref} />
      {orientationHint && (
        <p className="orientation-hint">Melhor experiência na horizontal — gire o dispositivo.</p>
      )}
      {showPad && (
        <div className="virtual-pad">
          {dPad.map((button) => (
            <button
              key={button.className}
              className={`virtual-pad-button ${button.className}`}
              onPointerDown={startDirection(button.dx, button.dy)}
              onPointerUp={stopDirection}
              onPointerLeave={stopDirection}
              onPointerCancel={stopDirection}
              type="button"
              aria-label={`Mover ${button.label}`}
            >
              {button.label}
            </button>
          ))}
        </div>
      )}
    </div>
  );
}
